.main-display {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: var(--side-margin);
  padding-top: 2.5em;
}

.header {
  width: 50%;
}

.header > h1 {
  margin-top: 0;
  margin-bottom: 0.5em;
}

.image-container {
  width: 45%;
}

.image-container > img {
  width: 100%;
}

.buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 2em;
}

@media screen and (max-width: 600px) {
  .main-display {
    display: flex;
    flex-direction: column;
  }

  .header {
    width: 100%;
  }

  .image-container {
    margin-top: 2em;
    width: 80%;
  }

  .image-container > img {
    width: 100%;
  }
}
