.bottomnav {
  width: var(--side-margin);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.bottomnav__group {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 1em;
}

.bottomnav-top {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /* padding: 20px; */
}

.bottomnav__logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: var(--medium-font-size);
  flex-grow: 1;
}

.bottomnav__logo > img {
  width: 4.5em;
  height: 4.5em;
}

.divider {
  width: 100%;
  height: 1px;
  background-color: var(--primary);
}

h4 > a {
  color: var(--black);
  text-decoration: none;
  cursor: pointer;
  text-transform: uppercase;
}

h4 > a:hover {
  text-decoration: underline;
}
