.jakdziala {
  width: var(--side-margin);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3em 0;
}

.jakdziala > h2 {
  margin-top: 0;
}

.jakdziala-content {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.jakdziala-column-inside {
  width: 35%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.jakdziala-column-inside > video {
  width: 100%;
}

.jakdziala-column-outside {
  width: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.jakdziala-step {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-bottom: 1em;
}

.jakdziala-step > img {
  width: 100%;
}
