.button {
  display: inline-block;
  padding: 0.6em 0em;
  font-size: var(--small-font-size);
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  border-radius: 3em;
  border: solid 0.1em;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  background-color: var(--secondary);
}

.button:hover {
  background-color: #0062cc;
}
