.ProductBig {
  background-color: var(--primary);
  border: solid 1px var(--secondary);
  border-radius: 1em;
}

.ProductBig > div {
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.ProductBig > div > h2 {
  font-size: var(--small-font-size);
  margin-bottom: 0em;
}

.ProductBig > div > p {
  font-size: var(--small-small-font-size);
  height: 1em;
  margin: 0 0 0.5em 0;
}

.ProductBig > div > img {
  width: 80%;
  height: auto;
  margin-bottom: 1em;
}

.Productbig-price {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 0.5em 0;
}

.Productbig-price > h3 {
  font-size: var(--small-font-size);
  font-weight: 400;
  margin: 0;
}

.Productbig-price > p {
  font-size: var(--small-small-font-size);
  font-weight: 400;
  text-decoration: line-through;
  margin: 0;
  margin-right: 1em;
}
