.navfont-container {
  padding: 10px 0;
}

.navfont {
  text-decoration: none;
  cursor: pointer;
  text-transform: uppercase;
  position: relative;
}

.navfont-margin-left {
  margin-left: 5px;
  padding-left: 10px;
}

.navfont-margin-right {
  margin-right: 5px;
  padding-right: 10px;
}

.navfont::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.3s ease-in-out;
}

.navfont-black::after {
  background-color: var(--text-black);
}

.navfont-white::after {
  background-color: var(--text-white);
}

.navfont:hover::after {
  width: 100%;
}
