.navbar {
  display: flex;
  width: var(--side-margin);
  justify-content: space-between;
  align-items: center;
  color: #fff;
  position: relative;
}

.navbar__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: var(--medium-font-size);
  flex-grow: 1;
}

.navbar__logo > img {
  width: 3.5em;
  height: 3.5em;
}

.navbar-menu-icon-container {
  position: absolute;
  left: 5%;
}

.navbar-menu-icon-container > img {
  width: 2.5em;
  height: 2.5em;
  cursor: pointer;
}

.navbar__group {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbar__group-left {
  left: 0px;
}

.navbar__group-right {
  right: 0;
}

.navbar__group > a > img {
  width: 2em;
  height: 2em;
  cursor: pointer;
}

.navbar-phone-basket {
  position: absolute;
  right: 5%;
}

.navbar-phone-basket > img {
  width: 2.5em;
  height: 2.5em;
  cursor: pointer;
}

.sidemenu-container {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: var(--secondary);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 100;
  overflow-x: hidden;
  transition: 0.5s;
}

.sidemenu {
  height: calc(100vh - 3em);
  width: calc(100vw - 3em);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidemenu-cross {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.sidemenu-cross > img {
  width: 3.5em;
  height: 3.5em;
  cursor: pointer;
}

.sidemenu-header {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2em;
  margin-left: 1em;
}

.sidemenu-group {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 1em;
}

.sidemenu-group > a {
  color: var(--white);
  font-size: var(--medium-font-size);
  text-decoration: none;
  margin: 0.3em 0;
  cursor: pointer;
  text-transform: uppercase;
}
