body {
  background-color: var(--background);

  margin: 0;
  font-family: "Encode Sans", "Titillium Web", "DM Sans", "Noto Sans Kawi",
    "JetBrains Mono", sans-serif, "Maven Pro", sans-serif, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  --white: #fff;
  --grey: rgb(247, 247, 247);
  --black: black;
  --primary: #fffae5;
  --secondary: #a8948e;
  --text-black: black;
  --text-white: white;
  --phone: "500px";

  --side-margin: 65%;

  --huge-font-size: 3rem;
  --big-font-size: 2rem;
  --medium-font-size: 1.5rem;
  --medium-small-font-size: 1.2rem;
  --small-font-size: 1rem;
  --small-small-font-size: 0.8rem;
  --small-small-small-font-size: 0.7rem;

  --phone: 600px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: var(--huge-font-size);
}

h2 {
  font-size: var(--big-font-size);
}

h3 {
  font-size: var(--small-small-font-size);
  font-weight: 300;
}

h4 {
  font-size: var(--small-small-small-font-size);
  font-weight: 300;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  html {
    font-size: 15px;
  }

  body {
    --side-margin: 90%;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  html {
    font-size: 16px;
  }

  body {
    --side-margin: 90%;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  html {
    font-size: 18px;
  }

  body {
    --side-margin: 85%;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  html {
    font-size: 20px;
  }

  body {
    --side-margin: 75%;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  html {
    font-size: 20px;
  }

  body {
    --side-margin: 65%;
  }
}
